<template>
  <div class="testtopic">
    <div class="explain">
       社会适应能力，指的是一个人在心理上适应社会生活和社会环境的能力。社会适应能力的高低，从某种意义上说，表明一个人的心理成熟程度。<br />>
   下面的测验题目可以帮助你更好的了解自己的社会适应能力。你可以从题目后的答案中，选出最适合你的那个答案。
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p>{{item.id}}、 {{item.problem}} </p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio :label="item.A">A、{{item.one}}</el-radio>
          <el-radio :label="item.B">B、{{item.two}}</el-radio>
          <el-radio :label="item.C">C、{{item.three}}</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '假如你的朋友突然带来一个你最不喜欢的人到你的家里，你会：',
        A: 2,
        B: 1,
        C: 3,
        one: '表示惊奇',
        two: '将你的感觉完全隐藏',
        three: '暂时忍耐',
      },{
        id: 2,
        radio: '',
        problem: '对自己的某次失败，你会：',
        A: 2,
        B: 3,
        C: 1,
        one: '只要别人有兴趣随时都会告诉他',
        two: '只在谈话中顺便说出来',
        three: '决不说，怕被别人抓住弱点，对自己不利',
      },{
        id: 3,
        radio: '',
        problem: '当遇到困难的时候，你会：',
        A: 3,
        B: 2,
        C: 1,
        one: '毫不忧郁的向有关专家、老师征求意见',
        two: '经常向熟人请教',
        three: '很少麻烦别人',
      },{
        id: 4,
        radio: '',
        problem: '你骑自行车到一个比较远的地方去参加社交活动，中途找不到路标时，你会：',
        A: 2,
        B: 1,
        C: 3,
        one: '赶快查自带的地图',
        two: '大声埋怨，不知道何时才能到达目的地',
        three: '耐心等待过路车或等人走过时，问个清楚',
      },{
        id: 5,
        radio: '',
        problem: '当你选择衣服时，你会：',
        A: 1,
        B: 3,
        C: 2,
        one: '总是固定在一种款式上',
        two: '跟随新潮流，希望适合自己',
        three: '在选定之前，先听取陪同的朋友或售货员的意见',
      },{
        id: 6,
        radio: '',
        problem: '当你知道将要有不愉快的事情时，你会：',
        A: 1,
        B: 2,
        C: 3,
        one: '自己进入紧张的状态',
        two: '相信事实并不会比预料的厉害',
        three: '感觉完全有办法应付',
      },{
        id: 7,
        radio: '',
        problem: '在嘈杂混乱的环境里，你会：',
        A: 1,
        B: 2,
        C: 3,
        one: '总觉得很烦，不能静下来',
        two: '仍然能够集中精神学习，但效率降低了',
        three: '不受影响，照常学习',
      },{
        id: 8,
        radio: '',
        problem: '和别人内争吵起来时，你会：',
        A: 3,
        B: 1,
        C: 2,
        one: '能够有力的反驳对方',
        two: '常常语无伦次，事后才想起要如何反驳对方，可是已经晚了',
        three: '能够反驳，但是没有多大的力量',
      },{
        id: 9,
        radio: '',
        problem: '每次参加正式的考试或竞争，你会：',
        A: 3,
        B: 1,
        C: 2,
        one: '常常比平时的成绩更好些',
        two: '常常不如平时的成绩',
        three: '和平时的成绩差别不大，基本上是差不多。',
      },{
        id: 10,
        radio: '',
        problem: '必须在大庭广众面前说话时，你会：',
        A: 1,
        B: 2,
        C: 3,
        one: '因怯场，便不知所措或说话结巴',
        two: '感觉虽然难，但还是想方设法完成',
        three: '侃侃而谈',
      },{
        id:11,
        radio: '',
        problem: '对团体或社会性的集会，你会：',
        A: 1,
        B: 2,
        C: 3,
        one: '总是想领导讨论',
        two: '只有在知道讨论题目时才参加',
        three: '讨厌在集会上说话，所以不参加',
      },{
        id: 12,
        radio: '',
        problem: '受到别人的批评，你会：',
        A: 1,
        B: 3,
        C: 2,
        one: '想找机会来批评他',
        two: '想查明受批评的原因',
        three: '想直接了解一下批评的理由',
      },{
        id: 13,
        radio: '',
        problem: '当情况紧迫时，你会：',
        A: 3,
        B: 2,
        C: 1,
        one: '仍然能够注意到该注意的细节',
        two: '就粗心大意，丢三落四',
        three: '就慌慌张张',
      },{
        id: 14,
        radio: '',
        problem: '参加各种比赛时，赛场越激烈，群众越加油',
        A: 3,
        B: 1,
        C: 2,
        one: '你的成绩就越好',
        two: '你的成绩越上不去',
        three: '你的成绩不会受到影响。',
      },{
        id: 15,
        radio: '',
        problem: '碰到阻力和困难时，你会：',
        A: 1,
        B: 2,
        C: 3,
        one: '经常改变既定的主意',
        two: '不改变既定的主意',
        three: '越有干劲',
      },{
        id: 16,
        radio: '',
        problem: '你符合下面哪一种情况：',
        A: 3,
        B: 1,
        C: 2,
        one: '不安于现状，总想改变点什么',
        two: '凡事只求讲"规范"，不做破格的事情',
        three: '礼貌要讲，但事情也要做。',
      },{
        id: 17,
        radio: '',
        problem: '你赞成下面哪一种说法：',
        A: 3,
        B: 1,
        C: 2,
        one: '只要是正确的，就坚持敢于孤立',
        two: '在矛盾方面让一让，就过去了',
        three: '尽量平和，把批评和斗争降到不得已的程度',
      },{
        id: 18,
        radio: '',
        problem: '假如自己被登报了，你会：',
        A: 3,
        B: 1,
        C: 2,
        one: '有点自豪，但是并不以为然',
        two: '很高兴，想让朋友也看看',
        three: '完全不感兴趣',
      },{
        id: 19,
        radio: '',
        problem: '为了给人留下好印象，你会：',
        A: 2,
        B: 3,
        C: 1,
        one: '想方设法，并花费一定的时间来考虑计划',
        two: '不特意去做，但是有机会就利用',
        three: '根本不想在别人面前做这种事情',
      },{
        id: 20,
        radio: '',
        problem: '你同意下面哪一种观点：',
        A: 3,
        B: 2,
        C: 1,
        one: '为了深入了解自己的国家，学习外国的东西是件好事',
        two: '外国的事和我们没有任何关系',
        three: '我比以往早醒1-学习外国的东西比学习本国的东西更感兴趣。',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "社会适应性自测量表"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 25 ){
          describe = "你的适应能力较差，你需要在今后的学习、生活、工作中，有意识的培养自己在这方面的能力，以提高心理承受力和适应能力"
        }else if( count > 24 && count < 37 ){
          describe = "你的适应能力属于一般。这表明你在进入新的环境后，经过一段时间的努力基本上就能够达到适应。"
        }else if( count > 36 && count < 49 ){
          describe = "你的适应能力较强。你能够较好的适应环境"
        }else if( count > 48 ){
          describe = "你的社会适应能力很强。你能够很快的适应新的学习，工作，生活环境，与人交往轻松、大方，给人的印象良好。你无论是到什么样的环境，都可以应付自如。"          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




