<template>
  <div class="testtopic2">
    <div class="explain">
       本量表含有21道关于焦虑一般症状的问题，请仔细阅读每一道题，指出最近一周内（包括当天），被各种症状烦扰的程度，并按以下标准进行选择：选1表示“无”；选2表示“轻度，无多大烦扰”；选择3表示“中度，感到不适但尚能忍受”；选4表示“重度，只能勉强忍受。”
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="items in databiao" :key="items.id">
        <p> {{items.problem}} </p>
        <el-radio-group v-model="items.radio" @change="change2">
          <el-radio class="class" :label="0">{{items.one}}</el-radio>
          <el-radio :label="1">{{items.two}}</el-radio>
          <el-radio :label="2">{{items.three}}</el-radio>
          <el-radio :label="3">{{items.four}}</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic2',
  data () {
    return {
      databiao: [{
        id: 1,
        radio: '',
        problem: '一、麻木或刺痛',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 2,
        radio: '',
        problem: '二、感到发热',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 3,
        radio: '',
        problem: '三、腿部颤抖',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 4,
        radio: '',
        problem: '四、不能放松',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 5,
        radio: '',
        problem: '五、害怕发生不好的事情',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 6,
        radio: '',
        problem: '六、头晕',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 7,
        radio: '',
        problem: '七、心悸或心率加快',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 8,
        radio: '',
        problem: '八、心神不定',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 9,
        radio: '',
        problem: '九、惊吓',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 10,
        radio: '',
        problem: '十、紧张',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id:11,
        radio: '',
        problem: '十一、窒息感',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 12,
        radio: '',
        problem: '十二、手发抖',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 13,
        radio: '',
        problem: '十三、摇晃',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 14,
        radio: '',
        problem: '十四、害怕失控',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 15,
        radio: '',
        problem: '十五、呼吸困难',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 16,
        radio: '',
        problem: '十六、害怕快要死去',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 17,
        radio: '',
        problem: '十七、恐慌',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 18,
        radio: '',
        problem: '十八、消化不良或腹部不适',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 19,
        radio: '',
        problem: '十九、昏厥',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 20,
        radio: '',
        problem: '二十、脸发红',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      },{
        id: 21,
        radio: '',
        problem: '二十一、出汗（不是因暑热冒汗）',
        one: '1',
        two: '2',
        three: '3',
        four: '4'
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "贝壳焦虑量表"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.databiao.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 45 ){
          describe = "非焦虑阳性"
        }else{
          describe = "焦虑阳性"
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic2{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




