<template>
  <div class="articleinfo">
    <div class="time">{{time}}</div>
    <div class="info">
      <p v-html="info"></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'articleinfo',
  data () {
    return {
      url: '',
      time: '',
      info: ''
    }
  },
  created() {
    var id = this.$route.query.id
    var token = this.$route.query.token
    sessionStorage.setItem("apptoken", token);
    console.log(id);
    this.$http.get('api/wiki/articledetail',{id: id}
      ).then(res => {
          console.log(res);
          if(res.code === 200){
              this.time = res.data.updated_at
              this.info = res.data.content
              this.url = res.data.pic_url
          }
      }).catch(err => {
          console.log(err)
      })
  },
  methods: {
    ti() {
      
    }
  }
}
</script>

<style lang="scss">
.articleinfo{
  padding: 0 .5rem;
  .time{
    font-size: .28rem;
    color: #999;
    text-align: center;
    line-height: .5rem;
  }
  .info{
    p{
      margin-top: .15rem;
      text-indent: 2em;
      font-size: .28rem;
      color: #333;
      line-height: .44rem;
      img{
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}
</style>




