<template>
  <div class="testtopic">
    <div class="explain">
        以过去一周来计算
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p> {{item.problem}} </p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio class="class" :label="0">1.不足1天</el-radio>
          <el-radio :label="1">2.有1-2天</el-radio>
          <el-radio :label="2">3.有3-4天</el-radio>
          <el-radio :label="3">4.有5-7天</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '一、我因一些小事而烦恼',
      },{
        id: 2,
        radio: '',
        problem: '二、我不想吃东西，我胃口不好',
      },{
        id: 3,
        radio: '',
        problem: '三、即使家人和朋友帮助我，我仍然无法摆脱心中苦闷',
      },{
        id: 4,
        radio: '',
        problem: '四、我觉得不如多数人好',
      },{
        id: 5,
        radio: '',
        problem: '五、 我在做事时无法集中注意力',
      },{
        id: 6,
        radio: '',
        problem: '六、 我感到情绪低落',
      },{
        id: 7,
        radio: '',
        problem: '七、我感到做任何事都很费力',
      },{
        id: 8,
        radio: '',
        problem: '八、我感到前途没有希望',
      },{
        id: 9,
        radio: '',
        problem: '九、我觉得我的生活是失败的',
      },{
        id: 10,
        radio: '',
        problem: '十、我感到害怕',
      },{
        id:11,
        radio: '',
        problem: '十一、我的睡眠不好',
      },{
        id: 12,
        radio: '',
        problem: '十二、我感到不高兴',
      },{
        id: 13,
        radio: '',
        problem: '十三、比平时说话要少',
      },{
        id: 14,
        radio: '',
        problem: '十四、我感到孤单',
      },{
        id: 15,
        radio: '',
        problem: '十五、我觉得人们对我不太友好',
      },{
        id: 16,
        radio: '',
        problem: '十六、我觉得生活没有意思',
      },{
        id: 17,
        radio: '',
        problem: '十七、我曾哭泣',
      },{
        id: 18,
        radio: '',
        problem: '十八、我感到忧愁',
      },{
        id: 19,
        radio: '',
        problem: '十九、我感到人们不喜欢我',
      },{
        id: 20,
        radio: '',
        problem: '二十、我觉得我无法继续我的生活',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "Beck抑郁问卷"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 10 ){
          describe = "无抑郁症状"
        }else if( count > 9 && count < 20 ){
          describe = "可能有抑郁症状"
        }else if( count > 19 ){
          describe = "肯定是有抑郁症状，建议看心理医生。"          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




