<template>
  <div class="testtopic">
    <div class="explain">
       好多同学在考试前会有很多异常的反应，比如：心慌意乱、焦躁、疑神疑鬼……其实是因为大考在即，你开始有压力，变得紧张不安。现在让我们一同来做一些测试，看看你是否压力过大呢？
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p> {{item.problem}} </p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio class="class" :label="2">1.常常是</el-radio>
          <el-radio :label="1">2.偶尔是</el-radio>
          <el-radio :label="0">3.完全没有</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '一、平时不知为什么总觉得心慌意乱，坐立不安。',
      },{
        id: 2,
        radio: '',
        problem: '二、上床后，怎么也睡不着，即使睡着也容易惊醒。',
      },{
        id: 3,
        radio: '',
        problem: '三、经常做噩梦，惊恐不安，早晨醒来就感到倦怠无力、焦虑烦躁。',
      },{
        id: 4,
        radio: '',
        problem: '四、经常早醒1～2个小时，醒后很难再入睡。',
      },{
        id: 5,
        radio: '',
        problem: '五、学习的压力常使自己感到非常烦躁，讨厌学习。',
      },{
        id: 6,
        radio: '',
        problem: '六、读书看报甚至在课堂上也不能专心一致，往往自己也搞不清在想什么。',
      },{
        id: 7,
        radio: '',
        problem: '七、遇到不称心的事情便较长时间地沉默少言。',
      },{
        id: 8,
        radio: '',
        problem: '八、感到很多事情不称心，无端发火。',
      },{
        id: 9,
        radio: '',
        problem: '九、哪怕是一件小事情，也总是很放不开，整日思索。',
      },{
        id: 10,
        radio: '',
        problem: '十、感到现实生活中没有什么事情能引起自己的兴趣，郁郁寡欢。',
      },{
        id:11,
        radio: '',
        problem: '十一、老师讲概念，常常听不懂，有时懂得快忘得也快。',
      },{
        id: 12,
        radio: '',
        problem: '十二、遇到问题常常举棋不定，迟疑再三。',
      },{
        id: 13,
        radio: '',
        problem: '十三、经常与人争吵发火，过后又后悔不已。',
      },{
        id: 14,
        radio: '',
        problem: '十四、经常追悔自己做错的事，有负疚感。',
      },{
        id: 15,
        radio: '',
        problem: '十五、一遇到考试，即使有准备也紧张焦虑。',
      },{
        id: 16,
        radio: '',
        problem: '十六、一遇到挫折，便心灰意冷，丧失信心。',
      },{
        id: 17,
        radio: '',
        problem: '十七、非常害怕失败，行动前总是提心吊胆，畏首畏尾。',
      },{
        id: 18,
        radio: '',
        problem: '十八、感情脆弱，稍不顺心，就暗自流泪。',
      },{
        id: 19,
        radio: '',
        problem: '十九、自己瞧不起自己，觉得别人总在嘲笑自己。',
      },{
        id: 20,
        radio: '',
        problem: '二十、喜欢跟比自己年幼或能力不如自己的人一起玩或比赛。 ',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "心理压力小测试"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 5 ){
          describe = "心理非常健康，请你放心。"
        }else if( count > 4 && count < 9 ){
          describe = "大致还属于健康的范围，但应有所注意，也可以找老师或同学聊聊。"
        }else if( count > 8 && count < 16 ){
          describe = "你在心理方面可能有了一些障碍，应找朋友、老师或家长进行沟通疏导。"
        }else if( count > 15 && count < 21 ){
          describe = "亮黄牌，有可能心里压力比较大，应采取适当的方法进行调适，或找心理辅导老师帮助你。 "          
        }else if( count > 20 ){
          describe = " 亮红牌，由于复习期间压力太大，致使精神紧张，所以建议适当放松，可以选择一项自己喜欢的运动或活动，释放压力，舒缓情绪。这样才能轻松愉快地参加考试。"          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




