<template>
  <div class="testtopic">
    <div class="explain">
       Beck将抑郁表述为20个“症状--态度类别“，Beck量表的每个条目便代表一个类别。这些类别包括：心情、悲观、失败感、不满、惩罚感、自厌、自责、自杀意向、痛哭、易激感、社会退缩、犹豫不决、睡眼障碍、疲劳、体重减轻、活动受抑制、有关躯体的先占观念与性欲减退。其目的是评价抑郁的严重程度。对每个类别的描述分为四级，级别值为0-3分，20个类别的每类都分四级评分，总分范围0-63
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p>( {{item.problem}} )</p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio class="class" :label="0">1.{{item.one}}</el-radio>
          <el-radio :label="1">2.{{item.two}}</el-radio>
          <el-radio :label="2">3.{{item.three}}</el-radio>
          <el-radio :label="3">4.{{item.four}}</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '一',
        one: '感到忧郁。',
        two: '我感到忧郁或沮丧。',
        three: '我整天忧郁，无法摆脱。',
        four: '我十分忧郁，已经忍受不住。'
      },{
        id: 2,
        radio: '',
        problem: '二',
        one: '我对未来并不悲观失望。',
        two: '我感到前途不太乐观。',
        three: '我感到对前途不抱希望。',
        four: '我感到今后毫无希望，不可能有所好转。'
      },{
        id: 3,
        radio: '',
        problem: '三',
        one: '我并无失败的感觉。',
        two: '我觉得和大多数人相比我是失败的。',
        three: '回顾我的一生，我觉得那是一连串的失败。',
        four: '我觉得我是一个失败的人。'
      },{
        id: 4,
        radio: '',
        problem: '四',
        one: '我并不觉得有什么不满意。',
        two: '我觉得我不能像平时那样享受生活。',
        three: '任何事情都不能使我感到满意一些。',
        four: '我对所有的事情都不满意。'
      },{
        id: 5,
        radio: '',
        problem: '五',
        one: '我没有特殊的内疚感。',
        two: '我有时感到内疚或觉得自己没有价值。',
        three: '我感到非常内疚。',
        four: '我觉得自己非常坏，一钱不值。'
      },{
        id: 6,
        radio: '',
        problem: '六',
        one: '我没有对自己感到失望。',
        two: '我对自己感到失望。',
        three: '我讨厌自己。',
        four: '我憎恨自己。'
      },{
        id: 7,
        radio: '',
        problem: '七',
        one: '我没有要伤害自己的想法。',
        two: '我感到还是死掉的好。',
        three: '我考虑过自杀。',
        four: '有机会我就会自杀。'
      },{
        id: 8,
        radio: '',
        problem: '八',
        one: '我没失去和他人交往的兴趣。',
        two: '和平时相比，我和他人交往的兴趣有所减。',
        three: '我对他人丧失了大部分的兴趣。',
        four: '我对他人全无兴趣，也完全不理睬别人。'
      },{
        id: 9,
        radio: '',
        problem: '九',
        one: '我能像平时一样做出决定。',
        two: '我尝试避免做决定。',
        three: '对我而言，做出决断十分困难。',
        four: '我无法做出任何决断。'
      },{
        id: 10,
        radio: '',
        problem: '十',
        one: '我觉得我形象一点也不比过去糟。',
        two: '我担心我看起来老了，不吸引人了。',
        three: '我觉得我的外表肯定变了，变得不具吸引力。',
        four: '我感到我的形象丑陋且讨人厌。'
      },{
        id:11,
        radio: '',
        problem: '十一',
        one: '我能像平时那样工作。',
        two: '我做事时，花额外的努力才能开始。',
        three: '我必须努力强迫自己，方能干事。',
        four: '我完全不能做事情。'
      },{
        id: 12,
        radio: '',
        problem: '十二',
        one: '往相比，我并不容易疲倦。',
        two: '我比过去容易觉得疲乏。',
        three: '我做任何事都感到疲乏。',
        four: '我太易疲乏了，不能干任何事。'
      },{
        id: 13,
        radio: '',
        problem: '十三',
        one: '我的胃口不比过去差。',
        two: '我现在的胃口没有往常那样好。',
        three: '现在我的胃口比过去差多了。',
        four: '我一点食欲都没有。'
      },{
        id: 14,
        radio: '',
        problem: '十四',
        one: '我没有感到自己正在受惩罚。',
        two: '我感到我可能收惩罚。',
        three: '我预感我会受惩罚。',
        four: '我感到我正在受惩罚。'
      },{
        id: 15,
        radio: '',
        problem: '十五',
        one: '我感觉我并不别人差。',
        two: '我对自己的缺点和错误常自我反省。',
        three: '我经常责备自己的过失。',
        four: '每次发生糟糕的事情我都责备自己。'
      },{
        id: 16,
        radio: '',
        problem: '十六',
        one: '我并不比往爱哭。',
        two: '我现在比以前爱哭。',
        three: '现在我经常哭。',
        four: '我以往能哭，但现在即使我想哭也哭不出来'
      },{
        id: 17,
        radio: '',
        problem: '十七',
        one: '相比我并不过份担心自己身体健康。',
        two: '我担心字的身体的毛病如反胃和便秘。',
        three: '我很着急身体的毛病而妨碍我思考其他问题。',
        four: '我非常着急身体疾病，以至不能思考任何其他的事情。'
      },{
        id: 18,
        radio: '',
        problem: '十八',
        one: '我最近没有明显的体重减轻。',
        two: '我体重下降超过五斤。',
        three: '我体重下降超过10斤。',
        four: '我体重下降超过15斤，我在控制饮食来减轻体重。'
      },{
        id: 19,
        radio: '',
        problem: '十九',
        one: '我的性欲没有什么变化。',
        two: '我的性欲比以往差。',
        three: '现在我的性欲比以往减退了许多。',
        four: '我完全丧失了性欲。'
      },{
        id: 20,
        radio: '',
        problem: '二十',
        one: '我睡眼向以前一样好。',
        two: '我睡眼没有以往那样好。',
        three: '我比以往早醒1-2个小时，再入睡有困难。',
        four: '我比往常早醒几个小时，且不能再入睡。'
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "Beck抑郁问卷"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 5 ){
          describe = "无抑郁或极轻微"
        }else if( count > 4 && count < 14 ){
          describe = "轻度"
        }else if( count > 13 && count < 21 ){
          describe = "中度"
        }else if( count > 20 ){
          describe = "重度"          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




