<template>
  <div class="exercise">
    <div class="time">{{time}}</div>
    <div class="info">
      <video controls :src="info"></video>
    </div>
  </div>
</template>

<script>
export default {
  name: 'exercise',
  data () {
    return {
      time: '',
      info: ''
    }
  },
  created() {
    var id = this.$route.query.id
    var token = this.$route.query.token
    sessionStorage.setItem("apptoken", token);
    console.log(id,token);
    this.$http.get('box/sportvideodetail',{id: id}
      ).then(res => {
          console.log(res);
          if(res.code === 200){
              this.time = res.data.updated_at
              this.info = res.data.full_url
          }
      }).catch(err => {
          console.log(err)
      })
      this.$http.post('box/incviewnum',{video_id: id}
      ).then(res => {
          console.log(222,res);
          
      }).catch(err => {
          console.log(err)
      })
  },
  methods: {
    ti() {
      
    }
  }
}
</script>

<style lang="scss" scoped>
.exercise{
  padding: 0 .5rem;
  .time{
    font-size: .28rem;
    color: #999;
    text-align: center;
    line-height: .5rem;
  }
  p{
    margin-top: .15rem;
    text-indent: 2em;
    font-size: .28rem;
    color: #333;
    line-height: .44rem;
  }
  video{
    display: block;
    width: 6.3rem;
    height: 2.94rem;
    border-radius: .08rem;
  }
}
</style>




