<template>
  <div class="about">
    <img :src="url" alt="" srcset="">
    <div class="info">
      <p v-html="info"></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'about',
  data () {
    return {
      url: '',
      time: '',
      info: ''
    }
  },
  created() {
    var token = this.$route.query.token
    sessionStorage.setItem("apptoken", token);
    this.$http.get('company_info'
      ).then(res => {
          console.log(res);
          if(res.code === 200){
              this.url = res.data.pic_url
              this.info = res.data.content
          }
      }).catch(err => {
          console.log(err)
      })
  },
  methods: {
    
  }
}
</script>

<style lang="scss">
.about{
  padding: 0 .5rem;
  .time{
    font-size: .28rem;
    color: #999;
    text-align: center;
    line-height: .5rem;
  }
  p{
    font-size: .28rem;
    text-indent: 2em;
    color: #333;
    line-height: .44rem;
    margin-top: .15rem;
    img{
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 100%;
    }
  }
  img{
    display: block;
    width: 6.3rem;
    // height: 2.94rem;
    border-radius: .08rem;
  }
}
</style>




