            break;
          default:
            this.describe = "暂无可匹配职业。"
            break;
        }
        console.log(name,'this.describe',this.describe,ssss,nowDate);
        var ccc = ssss.join('');
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: ccc,describe: this.describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }   
        
