<template>
  <div class="testtopic">
    <div class="explain">
      以下测试，能帮助您了解自己目前的压力状况，如果您在近两个星期内有以下任何一种症状，请选择是
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p> {{item.problem}} </p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio class="class" :label="0">1.否</el-radio>
          <el-radio :label="1">2.是</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '一、经常患感冒，且不易治愈。',
      },{
        id: 2,
        radio: '',
        problem: '二、常有手脚发冷的情形。',
      },{
        id: 3,
        radio: '',
        problem: '三、手掌和腋下常出汗。',
      },{
        id: 4,
        radio: '',
        problem: '四、突然出现呼吸困难和苦闷窒息感。',
      },{
        id: 5,
        radio: '',
        problem: '五、有时心脏悸动现象。',
      },{
        id: 6,
        radio: '',
        problem: '六、有胸痛情况发生。',
      },{
        id: 7,
        radio: '',
        problem: '七、有头重感或头脑不清的昏沉感。',
      },{
        id: 8,
        radio: '',
        problem: '八、眼睛很容易疲劳。',
      },{
        id: 9,
        radio: '',
        problem: '九、有鼻塞现象。',
      },{
        id: 10,
        radio: '',
        problem: '十、站立时有发晕的情形。',
        one: '我觉得我形象一点也不比过去糟。',
        two: '我担心我看起来老了，不吸引人了。',
        three: '我觉得我的外表肯定变了，变得不具吸引力。',
        four: '我感到我的形象丑陋且讨人厌。'
      },{
        id:11,
        radio: '',
        problem: '十一、有头晕眼花的情形。',
      },{
        id: 12,
        radio: '',
        problem: '十二、有耳鸣的现象。',
      },{
        id: 13,
        radio: '',
        problem: '十三、口腔内有破裂或溃烂情形发生。',
      },{
        id: 14,
        radio: '',
        problem: '十四、经常喉痛。',
      },{
        id: 15,
        radio: '',
        problem: '十五、舌头上出现白苔。',
      },{
        id: 16,
        radio: '',
        problem: '十六、面对自己喜欢吃的东西，却毫无食欲。',
      },{
        id: 17,
        radio: '',
        problem: '十七、常觉得吃下的东西像沉积在胃里。',
      },{
        id: 18,
        radio: '',
        problem: '十八、有腹部发胀、疼痛感觉，而且常下痢、便秘。',
      },{
        id: 19,
        radio: '',
        problem: '十九、肩部很容易坚硬酸痛。',
      },{
        id: 20,
        radio: '',
        problem: '二十、背部和腰部经常疼痛。',
      },{
        id: 21,
        radio: '',
        problem: '二十一、疲劳感不易解除。',
      },{
        id: 22,
        radio: '',
        problem: '二十二、有体重减轻的现象。',
      },{
        id: 23,
        radio: '',
        problem: '二十三、稍微做一点事就马上感到很疲劳。',
      },{
        id: 24,
        radio: '',
        problem: '二十四、早上经常有起不来的倦怠感。',
      },{
        id: 25,
        radio: '',
        problem: '二十五、不能集中精力专心做事。',
      },{
        id: 26,
        radio: '',
        problem: '二十六、睡眠不好。',
      },{
        id: 27,
        radio: '',
        problem: '二十七、睡觉时经常做梦。',
      },{
        id: 28,
        radio: '',
        problem: '二十八、在深夜突然醒来时不易继续再睡着。',
      },{
        id: 29,
        radio: '',
        problem: '二十九、与人交际应酬变得很不起劲。',
      },{
        id: 30,
        radio: '',
        problem: '三十、稍有一点不顺心就会生气，而且有时有不安的情形发生。',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "Beck抑郁问卷"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 5 ){
          describe = "没什么压力"
        }else if( count > 4 && count < 11 ){
          describe = "轻微的压力"
        }else if( count > 10 && count < 21 ){
          describe = "有中度的压力"
        }else if( count > 20 && count < 26 ){
          describe = "有重度压力"          
        }else{
          describe = "压力相当严重" 
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




