<template>
  <div class="hearttest">
    <div class="box">
      <ul>
        <li class="te1"><router-link :to="{name: 'testtopic1'}"><p>01</p><span>抑郁自评量表</span></router-link></li>
        <li class="te2"><router-link :to="{name: 'testtopic2'}"><p>02</p><span>焦虑自评量表</span></router-link></li>
        <li class="te3"><router-link :to="{name: 'testtopic3'}"><p>03</p><span>性格自评量表</span></router-link></li>
        <li class="te4"><router-link :to="{name: 'testtopic4'}"><p>04</p><span>心里压力 <br /> 自评量表</span></router-link></li>
        <li class="te5"><router-link :to="{name: 'testtopic5'}"><p>05</p><span>心理健康<br /> 自评量表</span></router-link></li>
        <li class="te6"><router-link :to="{name: 'testtopic6'}"><p>06</p><span>CES－D抑郁<br />自评量表</span></router-link></li>
        <li class="te7"><router-link :to="{name: 'testtopic7'}"><p>07</p><span>EAP心理压力自评量表</span></router-link></li>
        <li class="te8"><router-link :to="{name: 'testtopic8'}"><p>08</p><span>霍兰德职业 <br />倾向测验量表</span></router-link></li>
        <li class="te9"><router-link :to="{name: 'testtopic9'}"><p>09</p><span>适应能力<br /> 评测量表</span></router-link></li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'hearttest',
  data () {
    return {
      value: ''
    }
  },
  beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    if (to.meta.title) {
      document.title = to.meta.title
    }
    next()
  },
  created() {
    var get = sessionStorage.getItem("apptoken");
    console.log('get',get);
    if(get == 'undefined'){
      var token = this.$route.query.token
      sessionStorage.setItem("apptoken", token);
      console.log('token',token);
    }
    
    
  }
}
</script>

<style lang="scss" scoped>
.hearttest{
  padding: .32rem;
  .box{
    box-shadow: 0 .1rem .2rem 0 rgba(153,153,153,0.2);
    border-radius: .08rem;
    padding: .23rem .6rem .75rem;
    ul{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      li{
        margin-top: .3rem;
        width: 1.72rem;
        height: 1.72rem;
        opacity: .9;
        border-radius: .08rem;
        a{
          display: block;
          width: 100%;
          height: 100%;
        }
        p{
          margin-top: .28rem;
          font-size: .36rem;
          color: #fff;
          text-align: center;
        }
        span{
          display: block;
          font-size: .2rem;
          color: #fff;
          padding: 0 .14rem;
          text-align: center;
          
        }
      }
      li.te1{
        background: #B998F3;
        box-shadow: 0 .1rem .2rem 0 rgba(202,178,245,1);
      }
      li.te2{
        background: #77BCF5;
        box-shadow: 0 .1rem .2rem 0 #77BCF5;
      }
      li.te3{
        background: #F8B8AA;
        box-shadow: 0 .1rem .2rem 0 #FAD3CB;
      }
      li.te4{
        background: #80EDE1;
        box-shadow: 0 .1rem .2rem 0 #AFF3EC;
      }
      li.te5{
        background: #DD93C8;
        box-shadow: 0 .1rem .2rem 0 #F4BBE3;
      }
      li.te6{
        background: #7BE293;
        box-shadow: 0 .1rem .2rem 0 #ABF4BC;
      }
      li.te7{
        background: #F3A699;
        box-shadow: 0 .1rem .2rem 0 #F9CEC8;
      }
      li.te8{
        background: #B194F8;
        box-shadow: 0 .1rem .2rem 0 #D4C3FE;
      }
      li.te9{
        background: #8FE3E9;
        box-shadow: 0 .1rem .2rem 0 #BCF4F8;
      }
    }
  }
}
</style>




