<template>
  <div class="testtopic">
    <div class="explain">
       下列 30项中，请选择“常常是”或“偶尔是”亦或是“完全没有”。因为这是你自己的事情，所以请坦率地选择。
    </div>
    <div class="info">
      <p>题目：</p>
      <div class="box" v-for="item in data" :key="item.id">
        <p>{{item.id}}{{item.problem}}</p>
        <el-radio-group v-model="item.radio" @change="change2">
          <el-radio class="class" :label="2">1.常常是</el-radio>
          <el-radio :label="1">2.偶尔是</el-radio>
          <el-radio :label="0">3.完全没有</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary">提交测试</el-button>
        <el-button @click="back" type="primary">重新选择</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
        id: 1,
        radio: '',
        problem: '、上床后，怎么也睡不着，即使睡着，也不能熟睡，只是做梦。',
      },{
        id: 2,
        radio: '',
        problem: '、心情焦躁不安，做事没有效率，情绪不停地变化，精力不集中，健忘——符合其中某项。',
      },{
        id: 3,
        radio: '',
        problem: '、懒得做任何事情，也没有干的精神，虽然很焦急，认为“这样不行”，但却仍然游手好闲，虚度光阴。',
      },{
        id: 4,
        radio: '',
        problem: '、与人见面感到麻烦。',
      },{
        id: 5,
        radio: '',
        problem: '、对诸如“口中积着唾液”、“自己的身体有怪味”或“有口臭”等这种事情很在意。',
      },{
        id: 6,
        radio: '',
        problem: '、某种想法浮现在脑海，难以忘记，怎么也排除不掉。 ',
      },{
        id: 7,
        radio: '',
        problem: '、毫无道理的失败、严重失败、不道德或粗暴的事情、犯罪——有做了其中某项的感觉。',
      },{
        id: 8,
        radio: '',
        problem: '、担心是否锁门和着火，躺在床上后，又起来确认，或刚一出门就返回之类的事情。',
      },{
        id: 9,
        radio: '',
        problem: '、脸红，或与人见面时，害怕给对方留下不愉快的印象的倾向',
      },{
        id: 10,
        radio: '',
        problem: '、一紧张，就出汗或血一下涌上头，身体莫名其妙地开始颤动。',
      },{
        id:11,
        radio: '',
        problem: '、高处、宽广场所、上锁的狭窄房屋、电梯、隧道、地道、拥挤的人群——害怕其中某项的倾向。',
      },{
        id: 12,
        radio: '',
        problem: '、害怕特定的动物、交道工具(电车、公共汽车)、尖状物及白色墙壁等稍微奇怪的东西的倾向',
      },{
        id: 13,
        radio: '',
        problem: '、感到被谁监视、被窥探或被人暗地说坏话。',
      },{
        id: 14,
        radio: '',
        problem: '、有某人想加害自己、企图陷害自己的感觉。',
      },{
        id: 15,
        radio: '',
        problem: '、例如，不触摸什么或不做占算之类的事情，就不能外出不能从事工作，不能上台阶；走路不隔一块基石迈步，就不舒服，以及与此类似的事情。',
      },{
        id: 16,
        radio: '',
        problem: '、例如，伏案工作时，数纸的页数，铅笔的支数；对其它事情也是如此，不计算就不行。',
      },{
        id: 17,
        radio: '',
        problem: '、从早饭到上班，或从回家到就寝，不按程序举行仪式就不行的倾向。',
      },{
        id: 18,
        radio: '',
        problem: '、一天必须洗几次手，公用电话的话筒不擦就不能使用，或对不洁极端在意。',
      },{
        id: 19,
        radio: '',
        problem: '、在鸦雀无声的集会或重要会议中，被想叫喊的冲动所驱使，或其他害怕某种冲动的倾向。',
      },{
        id: 20,
        radio: '',
        problem: '、站在经常有人自杀的著名场所、悬崖边、大厦顶、路边，有摇摇晃晃跳下去的感觉。',
      },{
        id: 21,
        radio: '',
        problem: '、面临担心的事情和困难的场面，就有呕吐、泻肚、胃痛、头痛、心脏病发作、发疼及 发热等症状。',
      },{
        id: 22,
        radio: '',
        problem: '、白天，突然被不可抗拒的睡眠所袭扰，无论怎样抵抗，还是睡着了。',
      },{
        id: 23,
        radio: '',
        problem: '、例如，报社记者不能整理文章，广播员广播出现错误，认为自己的职业和业务方面出现棘手的征兆。',
      },{
        id: 24,
        radio: '',
        problem: '、对心脏声音和呼吸作用非常介意，或为此难以成眠。',
      },{
        id: 25,
        radio: '',
        problem: '、突然感到心脏停止跳动，呼吸困难，要晕倒，或类似的事情。 ',
      },{
        id: 26,
        radio: '',
        problem: '、有虚构“灾难临头”或“遭受不幸”等畏惧事情的倾向。',
      },{
        id: 27,
        radio: '',
        problem: '、非常担心是否患癌症、脑疾病、公害病、成人病、种种传染病及其他疾病的倾向。',
      },{
        id: 28,
        radio: '',
        problem: '、悲观地看待诸事，无精打采，情绪忧郁、心情不好。',
      },{
        id: 29,
        radio: '',
        problem: '、认为自己不行，或给周围添麻烦，虽想活着，但又无可奈何。',
      },{
        id: 30,
        radio: '',
        problem: '、除以上例举的症状之外，被判断“自己一定是神经症”的时候。',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//返回上一层
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = "心理健康测试表"
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 6 ){
          describe = "请放心，你的心理非常健康。神经强韧。能顺利地适应现实。但是，也许过于强韧，请反省自己是否给周围的人“有点缺乏细致和灵活”的印象。"
        }else if( count > 5 && count < 14 ){
          describe = "大致还属于健康的范围。但是，你必须要改正“神经症与自己完全无关”的想法，你也有患神经症的可能性"
        }else if( count > 13 && count < 26 ){
          describe = "你在精神方面有些疲劳，应减少工作量，或通过休假和娱乐改变情绪，应采取适当的治疗方法。"
        }else if( count > 25 ){
          describe = "是黄牌警告。有可能患了神经症，建议你去看心理医生。如在这种自我诊断中得分较高，就立即断定自己是神经症还为时过早，但有必要找专门的医生进行细致的检查."          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'提交成功',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }else{
              this.$message.error(res.data.msg);
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'您还有题目没选择',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
.testtopic{
  .header{
    display: flex;
    justify-content: space-between;
    padding: 0 .7rem;
    font-size: .36rem;
    line-height: .9rem;
    i{
      line-height: .9rem;
      color: #5BD693;
    }
    .title{
      font-weight: 900;
      color: #000;
    }
  }
  .explain{
    padding: .18rem .45rem 0;
    font-size: .28rem;
    color: #333;
    line-height: .42rem;
    text-indent: 2em;
    border-top: 1px solid #ddd;
  }
  .info{
    box-sizing: border-box;
    margin-top: .48rem;
    border-top: 1px solid #ddd;
    color: #333;
    font-size: .3rem;
    padding: 0 .45rem;
    p{
      margin: .15rem 0;
    }
    .el-radio{
      display: block;
      width: 100%;
      line-height: .4rem;
      white-space: inherit;
    }
    .btn{
      text-align: center;
      .el-button{
        margin: .5rem auto;
      }
    }
  }
  
}
.el-message{
  min-width: 4rem;
}
</style>




